
















































import { Component, Prop, Vue } from "vue-property-decorator";
import MovieModel from "@/model/movie/Movie";
import MovieService from "@/service/movie/MovieService";

@Component({
  components: {},
})
export default class Movie extends Vue {
  @Prop() private movie!: MovieModel;
  private showPreview = false;

  get movieTemp() {
    return this.movie;
  }

  async LoadDetails() {
    await MovieService.loadDetails(this.movie);

    this.showPreview = !this.showPreview;
    console.log(this.movie.details);
  }
}
