










































































import { Component, Vue } from "vue-property-decorator";
import MovieService from "@/service/movie/MovieService";
import Movie from "@/components/Movie.vue";
import MovieList from "@/model/movie/MovieList";

@Component({
  components: {
    Movie,
  },
})
export default class Search extends Vue {
  private query = "";
  private page = 1;
  private data: MovieList | null = null;
  private order = { id: "title", text: "Tytuł" };
  private sort = { id: "ASC", text: "Rosnąco" };

  private orderList = [
    { id: "title", text: "Tytuł" },
    { id: "popularity", text: "Popularność" },
  ];

  private sortList = [
    { id: "ASC", text: "Rosnąco" },
    { id: "DESC", text: "Malejąco" },
  ];

  changeOrder(): void {
    if (this.order.id === "title") {
      if (this.sort.id === "ASC") {
        this.data?.results.sort((a, b) =>
          a.title.toLowerCase() > b.title.toLowerCase() ? 1 : -1
        );
      } else {
        this.data?.results.sort((a, b) =>
          a.title.toLowerCase() < b.title.toLowerCase() ? 1 : -1
        );
      }
    }

    if (this.order.id === "popularity") {
      if (this.sort.id === "ASC") {
        this.data?.results.sort((a, b) =>
          a.popularity > b.popularity ? 1 : -1
        );
      } else {
        this.data?.results.sort((a, b) =>
          a.popularity < b.popularity ? 1 : -1
        );
      }
    }
  }

  async clickPage(page: number): Promise<void> {
    this.page = page;
    await this.search();
  }

  async clickPreviousPage(): Promise<void> {
    if (this.page > 1) {
      this.page = this.page - 1;
      await this.search();
    }
  }

  async clickNextPage() {
    if (this.data && this.page < this.data?.total_pages) {
      this.page = this.page + 1;
      await this.search();
    }
  }

  async search(): Promise<void> {
    if (this.query === "") {
      this.data = null;
    }

    console.log(this.query);
    this.data = await MovieService.getList(this.query, this.page);
    this.changeOrder();
  }
}
